/* styles.css */

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(10px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.transition-transform {
  transition: transform 1s ease-in-out, opacity 0.5s ease-in-out;
}

.transform {
  transform: translateY(10px);
  opacity: 0;
}

.transform.translate-y-0 {
  transform: translateY(0);
  opacity: 1;
}

.writing-animation {
  font-family: 'Waiting for the Sunrise', cursive;
}

.writing-animation {
  font-family: "Kanit", sans-serif;
  font-weight: 800;
}

/* Block the style on mobile devices */
@media (max-width: 767px) {
  .writing-animation {
    font-family: initial;
    /* Reset to default or another font */
  }
}

/* Hide typing effect on mobile devices */
@media (max-width: 640px) {
  .typing-effect-container {
    display: none;
  }
}

.learn {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  margin: 20px auto;
  width: 250px;
  height: 250px;
  background: white;
  border-radius: 75px;
  font-family: 'Montserrat', sans-serif;
  font-size: 20px;
  font-weight: lighter;
  letter-spacing: 2px;
  transition: 1s box-shadow;
}

.learn:hover {
  box-shadow: 0 5px 35px 0px rgba(0, 0, 0, .1);
}

.learn:hover::before,
.learn:hover::after {
  display: block;
  content: '';
  position: absolute;
  width: 250px;
  height: 250px;
  background: #FDA8CF;
  border-radius: 75px;
  z-index: -1;
  animation: 1s clockwise infinite;
}

.learn:hover:after {
  background: #F3CE5E;
  animation: 2s counterclockwise infinite;
}

@keyframes clockwise {
  0% {
    top: -5px;
    left: 0;
  }

  12% {
    top: -2px;
    left: 2px;
  }

  25% {
    top: 0;
    left: 5px;
  }

  37% {
    top: 2px;
    left: 2px;
  }

  50% {
    top: 5px;
    left: 0;
  }

  62% {
    top: 2px;
    left: -2px;
  }

  75% {
    top: 0;
    left: -5px;
  }

  87% {
    top: -2px;
    left: -2px;
  }

  100% {
    top: -5px;
    left: 0;
  }
}

@keyframes counterclockwise {
  0% {
    top: -5px;
    right: 0;
  }

  12% {
    top: -2px;
    right: 2px;
  }

  25% {
    top: 0;
    right: 5px;
  }

  37% {
    top: 2px;
    right: 2px;
  }

  50% {
    top: 5px;
    right: 0;
  }

  62% {
    top: 2px;
    right: -2px;
  }

  75% {
    top: 0;
    right: -5px;
  }

  87% {
    top: -2px;
    right: -2px;
  }

  100% {
    top: -5px;
    right: 0;
  }
}